.App {
  text-align: center;
  position: fixed;
}


.App-logo {
  margin-left: 43%;
  padding:15px;
  height: 100px;
  pointer-events: none;
}


.Navbar{
  display: flex;
  padding: 10px;
  background-color:pink;
}

.Home{
  padding: 10px;
  padding-right: 20px;
  border-inline: 5px solid;
  border-right:5px solid brown;
}

.Gallery{
  padding: 10px;
  padding-left: 20px;
  color:brown;
}

.left{
  display: flex;
  margin-left: 8%;
}

.instagram {
  max-width: 40px;
}

.right{
  margin-left:70%;
}

.picture{
  margin-left:42%;
  margin-top: 25px;
  padding: 10px;
}

.photo{
     border:2px solid rgb(170, 224, 238);
     padding:10px;
     max-width: 200px;
     border-radius:10px;
}

.about{
  position: relative;
  margin-left:47.5%;
font-size: 30px;
}

.about-content{
  padding: 25px;
  max-width:750px;
  margin-left:23%;
  font-size:20px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

::-webkit-scrollbar{
  height: 1px;
  color:black;
  margin: 0;
  max-width:5px;
}

::-webkit-scrollbar-track {
  background: wheat; 
}
::-webkit-scrollbar-thumb {
  background: none; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: black; 
}